.Board {
  margin: 0 auto;
  border-radius: 10px;
  /* box-shadow: 10px 10px 0 red; */
  box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
  cursor: pointer;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}

table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}

table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}

table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  margin-bottom: 3vh;
}

.neon-orange {
  font-family: neon;
  color: #fb4264;
  font-size: 10vh;
  line-height: 9vw;
  text-shadow: 0 0 3vw #f40a35;
  animation: neon-orange 1s ease infinite;
  -moz-animation: neon-orange 1s ease infinite;
  -webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
  font-family: neon;
  color: #426dfb;
  font-size: 10vh;
  line-height: 9vw;
  text-shadow: 0 0 3vw #2356ff;
  animation: neon-blue 2s linear infinite;
  -moz-animation: neon-blue 2s linear infinite;
  -webkit-animation: neon-blue 2s linear infinite;
  -o-animation: neon-blue 2s linear infinite;
}

.winner .neon-blue,
.winner .neon-orange {
  font-size: 10vh;
}

@keyframes neon-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }

  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@keyframes neon-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }

  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

.Board-button {
  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: 5vh;
}

button {
  border: none;
  font-family: inherit;
  font-size: 1.4rem;
  cursor: pointer;
  width: auto;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #00bcc4;
  color: #fff;
  box-shadow: 0 6px #0097a7;
  border-radius: 5px;
  padding-top: 4px;
}

button:hover {
  background-color: #0097a7;
  box-shadow: 0 6px #096670;
}

button:active {
  box-shadow: 0 6px #0097a7;
  background-color: #3f71aa;
  transform: translateY(4px);
}

.Board-button-won {
  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: 100px;
}

@media only screen and (max-width: 1024px) {
  .neon-orange {
    font-size: 10vh;
    margin-bottom: 5vh;
  }
  .neon-blue {
    font-size: 10vh;
    margin-bottom: 5vh;
  }
}

@media only screen and (max-width: 768px) {
  .neon-orange {
    font-size: 9vh;
  }
  .neon-blue {
    font-size: 9vh;
  }
}

@media only screen and (max-width: 420px) {
  .neon-orange {
    font-size: 8vh;
  }
  .neon-blue {
    font-size: 8vh;
    margin-top: 2vh;

  }
  .Board-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3vh;
  }
  button {
    font-size: 2vh;
  }
}

@media only screen and (max-width: 380px) {
  .neon-orange {
    font-size: 8vh;
  }
  .neon-blue {
    font-size: 8vh;
    margin-top: 1vh;
  }
  .Board-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 10vh;
  }
}

@media only screen and (max-width: 325px) {
  .neon-orange {
    font-size: 10vh;
  }
  .neon-blue {
    font-size: 10vh;
    margin-top: 2vh;
  }
  .Board-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1vh;
  }
  button {
    font-size: 1rem;
  }
}